.bottom_nav_container
{
    width: 100%;
    height:max-content;
    margin: 0px;
    padding:2px;
    bottom: 0px;
    position: fixed;
    box-sizing: border-box;
    background-color:#086aa3;
    z-index:999;
}

.bottom_nav_wrapper
{
    width: 100%;
    height:max-content;
    margin: 0px;
    padding:3px;
    box-sizing: border-box;
}


.bottom_nav_list_container
{
    margin: 0px;
    height:max-content;
    padding:4px;
    box-sizing: border-box;
    display: flex;
    list-style: none;
    justify-content: space-around;
}

.bottom_nav_list_link
{
   text-decoration: none;
   display:flex;
   flex-direction: column;
   justify-content: center;
   color: inherit;
}

.bottom_nav_list_link:-webkit-any-link {
    background-color: transparent;
    color: inherit;
}

.bottom_nav_list_link_title
{
    width:100%;
    margin-top: 5px;
    margin-bottom:0px;
    padding:0px;
    box-sizing: border-box;
    text-align: center;
    color: rgb(0, 0, 0);
    font-family: Arial, Helvetica, sans-serif;
}

.bottom_nav_list_link_icon
{
    width:32px;
    height:32px;
    margin: auto;
    opacity:1;
}

.bottom_nav_list_link:focus
{
    background-color: transparent;
    opacity:1;
}

.bottom_nav_list_link_icon:focus
{
  background-color: transparent;
  opacity:1;
}