.search_bar_outer_container
{
    width: 100%;
    height:30px;
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

.search_bar_wrapper
{
    width: 100%;
    height:30px;
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

.search_bar_input
{
    width: 100%;
    height:40px;
    margin: 0px;
    padding:20px;
    box-sizing: border-box;
    text-align: left;
    border: 1px solid rgb(233, 151, 20);
  /*  background-image: url(./icons/search.png);
    background-size: 32px;
    background-repeat: no-repeat;
    background-position: center right; 
    background-position: center;
    background-position-y: 50px;*/
    font-size: 1.3rem;
    border-radius: 20px;
    overflow: hidden;
    outline: none;
}

.search_bar_input:focus
{
    outline: none;
    border:  1px solid rgb(233, 151, 20);
}