.header_outer_container
{
    width: 100%;
    height: max-content;
    margin: 0px;
    padding:2px 20px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    background-color:#086aa3;
}

.header_logo_section
{
    width: 100%;
    height:max-content;
    margin: 0px;
    padding:3px 0px;
    box-sizing: border-box;
}

.header_logo_section h2
{
    width: max-content;
    margin: auto;
    margin-top:5px;
    margin-bottom: 5px;
    font-size: 1.8rem;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
    color: white;
}