.main_image_outer_container
{
    width: 100%;
    height:40vh;
    margin:0px;
    padding: 0px;
    box-sizing: border-box;
}

.main_image_body
{
    width: 100%;
    height:100%;
    margin: 0px;
    padding: 0px;
    padding-top: 1px;
    box-sizing: border-box;
    background-image: url(https://images.oyoroomscdn.com/uploads/hotel_image/42030/xlarge/3273f03a218a04da.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}

.main_image_heading
{
    width: max-content;
    margin:0px;
    padding:0px;
    margin: auto;
    font-size: 1.6rem;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
    color: #ffffff;
    text-align: center;
    margin-top:70px;
}