.login_page_outer_container
{
    width: 100%;
    margin: 0px;
    padding:20px 0px;
    box-sizing: border-box;
    margin-bottom: 50px;
}

.login_page_wrapper
{
    width: 100%;
    margin: 0px;
    padding:0px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.login_page_title
{
    width: 100%;
    margin:10px 0px 40px 0px;
    font-size: 2.2rem;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 600;
    text-align: center;
}

.login_options_container
{
    width: 100%;
    height: max-content;
    margin: 0px;
    padding:0px;
    box-sizing: border-box;
    display: flex;
    flex-direction:column;
}

.login_option
{
    width: 100%;
    height: max-content;
    margin: 0px;
    padding:0px;
    box-sizing: border-box;
    display: flex;
    flex-direction:column;
    justify-content: center;
}

.login_option_link
{
   margin: auto;
   margin-top:10px;
}

.login_option_or
{
    width: 100%;
    text-align: center;
    font-size: 1.1rem;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 600;
}

.option_button
{
    width:260px;
    margin: auto;
    height:40px;
    border: 0px;
    outline: none;
    font-size: 1rem;
    font-family: Arial, Helvetica, sans-serif;
}


.option_button:focus
{
    opacity:.8;
}