.hotel_card_outer_container
{
    width:300px;
    height: max-content;
    margin: 0px;
    padding:5px;
    box-sizing: border-box;
   
}

.hotel_card_outer_wrapper
{
    width: 100%;
    height:max-content;
    margin: 0px;
    padding: 0px;
    background-color: #fdf5e6;
    padding-bottom:20px;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    -webkit-box-shadow: 3px 2px 2px 0px rgba(0,0,0,0.42);
    -moz-box-shadow: 3px 2px 2px 0px rgba(0,0,0,0.42);
    box-shadow: 3px 2px 2px 0px rgba(0,0,0,0.42);
    border-radius: 20px;
    overflow: hidden;
}

.hotel_card_image
{
    width: 100%;
    height:180px;
}

.hotel_card_ratings
{
    width: 100%;
    height: max-content;
    margin: 0px;
    text-align: left;
    padding-left:40px;
    color: rgb(255, 136, 0);
    margin-top: -40px;
    box-sizing: border-box;
}

.hotel_card_title
{
    width:100%;
    margin:1px;
    text-align: left;
    padding:0px;
    padding-left: 20px;
    font-size: 1em;
    font-weight: 800;
    box-sizing: border-box;
    margin-top:10px;
    font-family: Arial, Helvetica, sans-serif;
}

.hotel_card_price
{
    width:100%;
    margin: 0px;
    text-align: left;
    padding:0px;
    padding-left: 20px;
    font-size: .9em;
    font-weight: 300;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 5px;
}

.hotel_card_address
{
    width:100%;
    margin: 0px;
    text-align: left;
    padding:0px;
    padding-left: 20px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: .75em;
    font-weight: 300;
    margin-top: 10px;
    box-sizing: border-box;
}

