.react-rater {
  display: inline-block;
  line-height: normal; }
  .react-rater > * {
    display: inline-block; }

.react-rater-star {
  cursor: pointer;
  color: rgb(255, 255, 255);
  position: relative; }
  .react-rater-star.will-be-active {
    color: rgb(255, 255, 255); }
  .react-rater-star.is-active {
    color: rgb(255, 196, 2); }
  .react-rater-star.is-active-half:before {
    color: rgb(255, 196, 2);
    content: '\2605';
    position: absolute;
    left: 0;
    width: 50%;
    overflow: hidden; }
  .react-rater-star.is-disabled {
    cursor: default; }
