.card_slider_outer_container
{
    width: 100%;
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}

.card_slider_title
{
 width: 100%;
 font-size: 1.2rem;
 font-weight: 550;
 font-family: Arial, Helvetica, sans-serif;
 padding-left: 20px;
 box-sizing: border-box;
}

.card_slider_body_container
{
  width: 100%;
  height: max-content;
  margin:0px;
  padding: 0px;
}

.card_slider_slide
{
    width: 100%;
    height:max-content;
    margin:0px;
    padding: 0px;
}